@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
.App{display:flex;flex-direction:column}.App__desktop{min-height:calc(100vh - 35px);background-image:url(/static/media/background.586d0a60.jpg);background-repeat:no-repeat;background-size:cover;display:flex}.App__panel{display:flex;align-items:center;height:35px;background-color:#c0c0c0}.App__panel-button{margin-left:2px;margin-right:2px;height:30px;justify-content:flex-start !important;width:160px}.App__panel-button>div{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.App__panel-separator{border-left:1px solid #808080;border-right:1px solid #808080;margin-left:3px;margin-right:3px;height:26px}.App__panel-clock{border:2px solid #000000;border-image:url(data:image/png;base64,);border-image-slice:2 2 2 2;margin-right:4px;display:flex;align-items:center;justify-content:center;height:26px;font-family:"Open Sans", sans-serif;font-size:14px;padding-left:4px;padding-right:4px}

.Window{border:10px solid #000;border-image:url(data:image/png;base64,);border-image-slice:10 10 10 10;height:1%;display:flex}.Window__content{display:inline-block;background-color:#c0c0c0}.Window__separator{border-top:1px solid #808080;border-bottom:1px solid #ffffff;margin-left:3px;margin-right:3px}.Window__header{background-color:#000080;width:auto;height:18px;margin:2px;display:flex;flex-direction:row;align-items:center;justify-content:space-between}.Window__header-left{display:flex;flex-direction:row;align-items:center;margin-left:3px}.Window__title{font-size:11px;font-family:"Open Sans", sans-serif;color:white;font-weight:700;margin-left:4px;cursor:default;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Window__icon{-ms-interpolation-mode:nearest-neighbor;image-rendering:-moz-crisp-edges;image-rendering:pixelated;image-rendering:crisp-edges;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}.Window__header-right{-ms-interpolation-mode:nearest-neighbor;image-rendering:-moz-crisp-edges;image-rendering:pixelated;image-rendering:crisp-edges;display:flex;margin-right:1px;margin-left:4px}.Window__header-minimize{border:0;height:16px;width:16px;margin-right:2px;background:transparent url(data:image/png;base64,)}.Window__header-minimize:active{background:transparent url(data:image/png;base64,)}.Window__header-maximize{border:0;height:16px;width:16px;margin-right:2px;background:transparent url(data:image/png;base64,)}.Window__header-maximize:active{background:transparent url(data:image/png;base64,)}.Window__header-close{border:0;height:16px;width:16px;margin-right:2px;background:transparent url(data:image/png;base64,)}.Window__header-close:active{background:transparent url(data:image/png;base64,)}.Window__container{margin:2px 4px 4px;border:2px solid #000000;border-image:url(data:image/png;base64,);border-image-slice:2 2 2 2}

.MediaPlayer{display:flex;flex-direction:column}.MediaPlayer__image{width:352px;margin:4px;border:2px solid #000000;border-image:url(data:image/png;base64,);border-image-slice:2 2 2 2}.MediaPlayer__bottom{display:flex;flex-direction:row}.MediaPlayer__timer{width:200px;height:50px;background-color:black;border:2px solid #000000;border-image:url(data:image/png;base64,);border-image-slice:2 2 2 2;margin:4px;font-family:"More Perfect DOS VGA", sans-serif;color:#808000;display:flex;align-items:center;justify-content:center;font-size:32px}.MediaPlayer__control{margin:4px;display:flex;flex-direction:column;justify-content:space-between}.MediaPlayer__buttons{display:flex;flex-direction:row}.MediaPlayer__track{width:140px;border:2px solid #000000;border-image:url(data:image/png;base64,);border-image-slice:2 2 2 2;height:24px;overflow:hidden;display:flex;align-items:center}.MediaPlayer__track>span{font-family:"Open Sans", sans-serif;font-weight:normal;display:inline-block;padding-left:100%;text-indent:0;-webkit-animation:marquee 10s linear infinite;animation:marquee 10s linear infinite}@-webkit-keyframes marquee{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0)}}@keyframes marquee{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0)}}

.Button{display:flex;align-items:center;justify-content:center;min-height:14px;min-width:14px;padding:2px;border-radius:0;transition:none;font-family:"Open Sans", sans-serif;font-weight:normal;color:#000000;outline-color:#000000;border:1px solid #ffffff;border-right-color:#000000;border-bottom-color:#000000;background-color:#c0c0c0;box-shadow:inset -1px -1px #808080, inset 1px 1px #dfdfdf}.Button:active,.Button:checked,.Button:disabled:active,.Button:disabled:checked{border-top-color:#000000;border-right-color:#ffffff;border-left-color:#000000;border-bottom-color:#ffffff;box-shadow:inset 1px 1px #808080}.Button:disabled>img{opacity:0.3}

body {
    margin: 0;
    background-image: url(/static/media/background.586d0a60.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

@font-face {
    font-family: "Less Perfect DOS VGA";
    src: url(/static/media/LessPerfectDOSVGA.abc1fb1a.ttf);
}

@font-face {
    font-family: "More Perfect DOS VGA";
    src: url(/static/media/MorePerfectDOSVGA.4745a97b.ttf);
}


